body .loading {
    overflow: hidden;
    height: 100vh;
}

.loader {
    .loader-inner {
        .image-block {
            position: absolute;
            transform-origin: center;
            display: flex;
            align-items: center;
            justify-content: center;
            &.image-1 {
                width: 400px;
                left: 16%;
                bottom: 14%;
                @media (max-width: $layout-breakpoint-xsmall) {
                    width: 200px;
                    left: 4%;
                    bottom: 28%;
                  }
            }
            &.image-2 {
                width: 300px;
                right: 12%;
                top: 8%;
                @media (max-width: $layout-breakpoint-small) {
                    width: 150px;
                    top: 16%;
                    right: 4%;
                  }
            }
            &.image-3 {
                max-width: 400px;
                width: 40%;
                right: 20%;
                bottom: 10%;
                @media (max-width: $layout-breakpoint-small) {
                    width: 200px;
                    right: 6%;
                    bottom: 32%;
                  }
            }
            &.image-4 {
                width: 280px;
                left: 14%;
                top: 12%;
                @media (max-width: $layout-breakpoint-small) {
                    width: 140px;
                    left: 6%;
                    top: 18%;
                }
            }
            img {
                width: 100%;
                object-fit: fill;
            }
        }
    }
}